.inputs {
  width: 400px;
  display: flex;
  flex-direction: column; }

input {
  padding: 4px;
  height: 20px;
  margin-left: 10px;
  margin-bottom: 4px; }

table {
  border-collapse: collapse; }

td {
  padding: 5px;
  border: solid 1px black; }

.table-wrap {
  width: 100%;
  overflow: auto; }

.tape {
  width: 700px;
  overflow-x: auto;
  display: flex;
  margin-bottom: 20px; }

.letter {
  width: 50px;
  height: 50px;
  border: solid 1px black;
  margin-right: 5px;
  justify-content: center;
  align-items: center;
  display: flex;
  flex-shrink: 0; }

.letter:last-child {
  margin-right: 0; }

.letter.current {
  background-color: yellow; }
